import { useTheme } from '@mui/material/styles';
import useMediaQuery from '@mui/material/useMediaQuery';
import {useState} from 'react'
import {Box, ListItem, Toolbar, List, ListItemIcon, ListItemButton , ListItemText} from '@mui/material';
import InboxIcon from '@mui/icons-material/Inbox';
import MailIcon from '@mui/icons-material/Mail';
import DashboardIcon from '@mui/icons-material/Dashboard';
import ViewStreamIcon from '@mui/icons-material/ViewStream';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import Drawer from '@mui/material/Drawer';
import CssBaseline from '@mui/material/CssBaseline';
import {Route, Routes} from 'react-router-dom'
import Dashboard from './components/Dashboard'
import CreateProductForm from './components/CreateProductForm'
import ProductsTable from './components/ProductsTable'
import OrdersTable from './components/OrdersTable'
import CustomersTable from './components/CustomersTable'

import {useNavigate} from 'react-router-dom'

const menu =[
    {name: "Dashboard", path: "/admin", icon: <DashboardIcon /> },
    {name: "Products", path: "/admin/products", icon: <ViewStreamIcon />},
    {name: "Customers", path: "/admin/customers", icon: <DashboardIcon /> },
    {name: "Orders", path: "/admin/orders", icon: <DashboardIcon /> },
    {name: "AddProducts", path: "/admin/product/create", icon: <DashboardIcon /> },
    //{name: "", path: " "},

]


const Admin = () => {
    const theme = useTheme()
    const isLargeScreen = useMediaQuery(theme.breakpoints.up("lg"))
    const [sideBarVisible, setSideBarVisible]= useState(false)
    const navigate = useNavigate()

    const drawer = (
        <Box sx = {{overflow: "auto", display: "flex", flexDirection: "column" , justifyContent: "space-between", height : "100%", bgcolor: '#242b2e', color: 'white'}}>
            {isLargeScreen && <Toolbar />}
            <List>
                {menu.map((item, index) => <ListItem key={item.name} disablePadding onClick= {()=> navigate(item.path)}>


                    <ListItemButton>
                        <ListItemIcon style={{ color: 'white' }}>
                            {item.icon}
                        </ListItemIcon>
                        <ListItemText>
                            {item.name}
                        </ListItemText>
                    </ListItemButton>
                    </ListItem>
                    )}
            </List>
            <List>
                <ListItem disablePadding >


                    <ListItemButton>
                        <ListItemIcon style={{ color: 'white' }}>
                            <AccountCircleIcon />

                        </ListItemIcon >
                        <ListItemText>Account</ListItemText>
                    </ListItemButton>
                    </ListItem>

            </List>
        </Box>
    )


    return (
        <div>

            {/*<Box sx  = {{display: `${isLargeScreen ? "flex": "block" } `}}>  */}
            <div className="flex h-[100vh] relative bg-[#242b2e]">
                <CssBaseline/>
                {/*   <Drawer sx={{height : '100vh', width : 240, flexShrink: 0}} variant='permanent'>
                    {drawer}
                </Drawer> */}
                <div className=" w-[15%] sticky top-0 p-5 shadow-lg shadow-gray-600" >
                    {drawer}
                </div>

                <div className="w-[85%] overflow-auto" sx={{overflow: 'scroll'}}>
                    <Routes>
                        <Route path='/' element={<Dashboard /> }></Route>
                        <Route path='/product/create' element={<CreateProductForm /> }></Route>
                        <Route path='/products' element={<ProductsTable /> }></Route>
                        <Route path='/orders' element={<OrdersTable /> }></Route>
                        <Route path='/customers' element={<CustomersTable /> }></Route>
                    </Routes>
                </div>

            </div>



        </div>
    )
}

export default Admin