import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import {Avatar, Button, Card, CardHeader} from '@mui/material'
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import { useDispatch, useSelector} from 'react-redux'
import {findProducts} from '../../State/Product/Action'
import {useEffect} from 'react'
import {deleteProduct} from '../../State/Product/Action'





const ProductsTableView = () =>{
    const dispatch = useDispatch()
    const {producta} = useSelector(store=>store)
    //console.log("products----",producta)

    const handleProductDelete = (productId) => {
        dispatch(deleteProduct(productId))
    }

    useEffect (() => {
        const data = {
            category: "",
            color: [],
            size: [],
            minPrice: 0,
            maxPrice: 10000000,
            minDiscount: 0,
            sort: "price_low",
            pageNumber: 1,
            pageSize: 20,
            stock: ""
        }

        //console.log("data for color:" , data)
        dispatch(findProducts(data))
    },[])

    return (
        <div className="p-5 text-white">
            <Card  sx={{bgcolor: '#242b2e', color: 'white'}} className='mt-2' >
                <CardHeader title='Recent Products'/>
                <TableContainer sx={{bgcolor: '#242b2e'}} component={Paper}>
                    <Table sx={{ minWidth: 650,  '& td, & th': {
                            color: 'white'
                        }}} aria-label="simple table">
                        <TableHead>
                            <TableRow>
                                <TableCell>Image</TableCell>
                                <TableCell align="">Title</TableCell>
                                <TableCell align="">ID</TableCell>
                                <TableCell align="">Category</TableCell>
                                <TableCell align="">Price</TableCell>
                                <TableCell align="">Quantity</TableCell>
                                
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {producta?.products?.content?.slice(0,5).map((item) => (
                                <TableRow
                                    key={item.title}
                                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                >
                                    <TableCell align="right">
                                        <Avatar src={item.imageUrl}></Avatar>
                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {item.title}
                                    </TableCell>

                                    <TableCell align="">{item._id}</TableCell>
                                    <TableCell align="">{item.category?.name}</TableCell>
                                    <TableCell align="">{item.price}</TableCell>
                                    <TableCell align="">{item.quantity}</TableCell>


                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            </Card>

        </div>
    )
}

export default ProductsTableView