import {FIND_PRODUCT_BY_ID_REQUEST, FIND_PRODUCT_BY_ID_SUCCESS, FIND_PRODUCT_BY_ID_FAILURE, FIND_PRODUCTS_REQUEST,  FIND_PRODUCTS_SUCCESS, FIND_PRODUCTS_FAILURE, CREATE_PRODUCT_REQUEST, CREATE_PRODUCT_SUCCESS, CREATE_PRODUCT_FAILURE, DELETE_PRODUCT_REQUEST, DELETE_PRODUCT_SUCCESS, DELETE_PRODUCT_FAILURE } from './ActionType'


const initialState = {
    products: [],
    product: null,
    loading: false,
    error: null,
}

export const customerProductReducer = (state = initialState, action) => {
    switch(action.type) {


        case FIND_PRODUCTS_REQUEST:
        case FIND_PRODUCT_BY_ID_REQUEST:
            return {...state, loading: true, error: null}

        case FIND_PRODUCTS_SUCCESS:
            return {...state, loading: false, products: action.payload}

        case FIND_PRODUCT_BY_ID_SUCCESS:
            //console.log("here the product is in reducer:::::::::",action.payload)
            return {...state, loading: false, product: action.payload, error: null}
        case DELETE_PRODUCT_SUCCESS:
            //console.log(action.payload, state.products.content[0]._id)
            return {...state, loading: false, error: null, products: {
                ...state.products,
                    content: state.products.content.filter((item)=> item._id !== action.payload )
                }}

        case FIND_PRODUCTS_FAILURE:
        case FIND_PRODUCT_BY_ID_FAILURE:
            return {...state, loading: false, error: action.payload}

        default:
            return state

    }
}