import {Grid} from '@mui/material'
import Achievements from './Achievements'
import ProductsTableView from '../view/ProductTableView'

import MonthlyOverview from './MonthlyOverview'
import OrderTableView from '../view/OrderTableView'

const Dashboard = () =>{
    return (
        <div className="p-10">
            <Grid container spacing={2}>
                <Grid item xs={12} md={4} className="p-5 shadow-lg shadow-gray-600" sx={{bgcolor: '#242b2e', color: 'white'}}>
                    <Achievements />
                </Grid>
                
                
                <Grid item xs={12} md={8} className="p-5 shadow-lg shadow-gray-600" sx={{bgcolor: '#242b2e', color: 'white'}}>
                    <MonthlyOverview />
                </Grid>
                <Grid item xs={12} md={12} className="p-5 shadow-lg shadow-gray-600" sx={{bgcolor: '#242b2e', color: 'white'}}>
                    <OrderTableView />
                </Grid>
                <Grid item xs={12} md={12} className="p-5" className="p-5 shadow-lg shadow-gray-600" sx={{bgcolor: '#242b2e', color: 'white'}}>
                    <ProductsTableView />
                </Grid>

            </Grid>
            
            
        </div>
    )
}

export default Dashboard