import {Grid, TextField, Button} from '@mui/material'
import {useNavigate} from 'react-router-dom'
import {useDispatch, useSelector} from 'react-redux'
import {login } from '../../State/Auth/Action'
import {useEffect} from "react";


const LoginForm = ()=>{
    const dispatch = useDispatch()
    const navigate = useNavigate()

   

    const handleSubmit = (event) =>{
        event.preventDefault()

        const data = new FormData(event.currentTarget)
        const userData = {

            email: data.get("email"),
            password : data.get("password")
        }
        console.log(userData)
        dispatch(login(userData))
    }
    return (
        <div>
            <form onSubmit = {handleSubmit}>
                <Grid container spacing={3}>

                    <Grid item xs={12}>
                        <TextField required id="email" name="email" label="Email" fullWidth autoComplete="email" />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField required id="password" name="password" label="Password" fullWidth autoComplete="password" type="password"/>
                    </Grid>
                    <Grid item xs={12}>
                        <Button className="bg-[#9155FD] w-full" type="submit" variant="contained" size="large" sx={{padding: ".8rem 0" , bgColor: "#9155FD"}}>
                            Login
                        </Button>
                    </Grid>
                </Grid>
            </form>

            <div className="flex justify-center flex-col items-center">
                <div className="py-3 flex item-center">
                    <p>If you do not have an account?</p>
                    <Button onClick={()=> navigate("/register")} className="ml-5" size="small">
                        Regiser
                    </Button>
                </div>
            </div>
        </div>
    )
}

export default LoginForm