import React from 'react'
import MainCarousel from "../../components/HomeCarousel/MainCarousel";
import HomeCardCarousel from "../../components/HomeCardCarousel/HomeCardCarousel";
import {mens_kurta} from "../../../data/menskurta";


const HomePage = () => {
    return (
        <div>
            <MainCarousel />

            <div className="space-y-10 py-20 flex flex-col justify-center px-5 lg:px-10">
                <HomeCardCarousel data={mens_kurta} sectionName={"Men's Kurta"}/>
                <HomeCardCarousel data={mens_kurta} sectionName={"Men's Shoes"}/>
                <HomeCardCarousel data={mens_kurta} sectionName={"Men's Shirts"}/>
                <HomeCardCarousel data={mens_kurta} sectionName={"Women's Saree"}/>
                <HomeCardCarousel data={mens_kurta} sectionName={"Women's Kurta"}/>


            </div>
        </div>
    )
}

export default HomePage