import React from 'react'
import AliceCarousel from "react-alice-carousel";
import  { useRef } from 'react';
import HomeSectionCard from "../HomeSectionCard/HomeSectionCard";
import KeyboardArrowLeftIcon from '@mui/icons-material/KeyboardArrowLeft'
import {useState} from "react";

import Button from '@mui/material/Button';




const HomeCardCarousel = ({data, sectionName}) => {

    const carouselRef = useRef(null);
    const [activeIndex, setActiveIndex] =  useState(0)
    //const [forceRerender, setForceRerender] = useState(false);


    //console.log(activeIndex, "this")
   // console.log(forceRerender)
    const responsive = {
        0: { items: 1 },
        568: { items: 2 },
        1024: { items: 4 },
    };
    //px-4 lg:px-8 border

    const slidePrev = () => {
        if (carouselRef.current) {
            carouselRef.current.slidePrev();
        }
        setActiveIndex((prevIndex) => prevIndex - 1);
    };

    const slideNext = () => {
        if (carouselRef.current) {
            carouselRef.current.slideNext();
        }
        setActiveIndex((prevIndex) => prevIndex + 1);
    };

    const handleSlideChanged = ({ item }) => {
        //console.log(`Active slide changed to index: ${item}`);
        setActiveIndex(item);
    };

    const items = data.slice(0, 10).map((item) => (
        <HomeSectionCard key={item.id} product={item} />
    ));

    return (
        <div className="border">
            <h2 className="text-2xl font-extrabold text-gray-800 py-5 ">{sectionName}</h2>
            <div className="relative p-10 mt-10">
                <AliceCarousel
                    ref={carouselRef}
                    responsive={responsive}
                    items={items}
                    disableDotsControls
                    disableButtonsControls
                    onSlideChanged={handleSlideChanged}
                    activeIndex={activeIndex}
                />
                {activeIndex!== items.length-4 && (


                    <Button variant="contained" className="z-50 bg-white"   sx={{ bgcolor: "white", position:"absolute", top:"8rem", right:"0rem", transform:"translateX(50%) rotate(90deg)" }}  aria-label="next" onClick={() => slideNext()}>
                        <KeyboardArrowLeftIcon sx={{transform:"rotate(90deg)", color: "black"}}/>
                    </Button>)

                }

                {activeIndex > 0 && (

                <Button variant="contained" className="z-50 bg-white"   sx={{ bgcolor: "white", position:"absolute", top:"8rem", left:"0rem", transform:"translateX(-50%) rotate(-90deg)" }}  aria-label="next" onClick={() => slidePrev()}>
                    <KeyboardArrowLeftIcon sx={{transform:"rotate(90deg)", color: "black"}}/>
                </Button>
                )
                }
            </div>

        </div>

    )
}

export default HomeCardCarousel