import api from '../../../config/apiConfig'


import { GET_ORDER_REQUEST,
    GET_ORDER_SUCCESS,
    GET_ORDER_FAILURE,
    CONFIRMED_ORDER_REQUEST,
    CONFIRMED_ORDER_SUCCESS,
    CONFIRMED_ORDER_FAILURE,
    PLACED_ORDER_REQUEST,
    PLACED_ORDER_SUCCESS,
    PLACED_ORDER_FAILURE,
    DELIVERED_ORDER_REQUEST,
    DELIVERED_ORDER_SUCCESS,
    DELIVERED_ORDER_FAILURE,
    CANCELLED_ORDER_REQUEST,
    CANCELLED_ORDER_SUCCESS,
    CANCELLED_ORDER_FAILURE,
    DELETE_ORDER_REQUEST,
    DELETE_ORDER_SUCCESS,
    DELETE_ORDER_FAILURE,
    SHIP_ORDER_REQUEST,
    SHIP_ORDER_SUCCESS,
    SHIP_ORDER_FAILURE } from './ActionType'

export const getOrders = () => {

    //console.log("reaching here in this code")
    return async (dispatch) => {
        dispatch({type: GET_ORDER_REQUEST})
        try{
            const response = await api.get('/api/admin/orders/')
            console.log(response.data)
            dispatch({type: GET_ORDER_SUCCESS, payload: response.data})
        }catch(error) {
        //console.log("catch error", error.message)
        dispatch({type: GET_ORDER_FAILURE, payload : error.message})
    }

    }

}

export const confirmOrder = (orderId) => async (dispatch) => {
    dispatch({type: CONFIRMED_ORDER_REQUEST})
    try{
        const response =  await api.put(`/api/admin/orders/${orderId}/confirm`)

        const data = response.data
        //console.log("confirmed the order ", data)
        dispatch({type: CONFIRMED_ORDER_SUCCESS, payload : data})
    }catch(error){
        dispatch({type: CONFIRMED_ORDER_FAILURE, payload : error.message})
    }
}

export const shipOrder = (orderId) => {
    return async(dispatch) => {
        try{
            dispatch({type: SHIP_ORDER_REQUEST})
            const {data} = await api.put(`api/admin/orders/${orderId}/ship`)
            //console.log("shipped order" , data)
            dispatch({type: SHIP_ORDER_SUCCESS,  payload: data})
        }catch(error){
            dispatch({type: SHIP_ORDER_FAILURE, payload: error.message})
        }
    }
}
export const deliverOrder = (orderId) => {
    return async(dispatch) => {
        try{
            dispatch({type: DELIVERED_ORDER_REQUEST})
            const {data} = await api.put(`api/admin/orders/${orderId}/deliver`)
            //console.log("deliver order" , data)
            dispatch({type: DELIVERED_ORDER_SUCCESS,  payload: data})
        }catch(error){
            dispatch({type: DELIVERED_ORDER_FAILURE, payload: error.message})
        }
    }
}
