import {useEffect } from 'react'
import {useDispatch , useSelector} from 'react-redux'
import {getOrders} from '../../State/Admin/Order/Action'


import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import {Avatar, Button, Card, CardHeader, AvatarGroup} from '@mui/material'
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import {confirmOrder, shipOrder, deliverOrder, deleteOrder} from '../../State/Admin/Order/Action'

import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';


import * as React from 'react';



const OrdersTableView = () =>{
    const dispatch = useDispatch()
    const {adminOrder} = useSelector(store => store)


    const [anchorEl, setAnchorEl] = React.useState(new Array(adminOrder?.orders?.length).fill(null) && []);
    const open = Boolean(anchorEl);
    const handleClick = (event, index) => {
        const newAnchorElArray = [...anchorEl]
        newAnchorElArray[index] = event.currentTarget
        setAnchorEl(newAnchorElArray);
    };
    const handleClose = (index) => {
        //console.log("handle close called", anchorEl[index])
        const newAnchorElArray = [...anchorEl]
        newAnchorElArray[index] = null
        setAnchorEl(newAnchorElArray);
        //console.log(anchorEl[index])
    };

    useEffect (() => {
        dispatch(getOrders())
    },[adminOrder.confirmed, adminOrder.shipped, adminOrder.delivered, adminOrder.deleteOrder])


    const handleShipped = (orderId, index)=> {
        dispatch(shipOrder(orderId))
        handleClose(index)
    }
    const handleConfirmed = (orderId, index)=> {
        dispatch(confirmOrder(orderId))
        handleClose(index)
    }

    const handleDelivered = (orderId, index)=> {
        dispatch(deliverOrder(orderId))
        handleClose(index)
        //console.log('id called::::::', orderId)
    }
    const handleDelete = (orderId)=> {
        dispatch(deleteOrder(orderId))
        handleClose()
    }

    return (
        <div className="p-5">

            <Card  sx={{bgcolor: '#242b2e', color: 'white'}} className='mt-2' >
                <CardHeader title='Recent Orders'/>
                <TableContainer sx={{bgcolor: '#242b2e'}} component={Paper}>
                    <Table sx={{ minWidth: 650,  '& td, & th': {
                            color: 'white'
                        }}} aria-label="simple table">
                        <TableHead>
                            <TableRow>
                                <TableCell>Image</TableCell>
                                <TableCell align="">Title</TableCell>
                                <TableCell align="">ID</TableCell>
                                <TableCell align="">Category</TableCell>
                                <TableCell align="">Price</TableCell>
                                <TableCell align="">Status</TableCell>

                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {adminOrder?.orders.slice(0,5).map((item, index) => (
                                <TableRow
                                    key={item.title}
                                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                >
                                    <TableCell align="right">
                                        <AvatarGroup max={3} sx={{justifyContent:"start"}}>
                                            {item.orderItems.map((orderItem) =>  <Avatar src={orderItem.product.imageUrl}></Avatar>)}
                                        </AvatarGroup>

                                    </TableCell>
                                    <TableCell component="th" scope="row">
                                        {item.orderItems.map((orderItem)=> <p>{orderItem.product.title};</p>)}
                                    </TableCell>

                                    <TableCell align="">{item._id}</TableCell>
                                    <TableCell align="">{item.category?.name}</TableCell>
                                    <TableCell align="">{item.totalPrice}</TableCell>
                                    <TableCell align=""><span className={`${item.orderStatus=='CONFIRMED'? "bg-[green]": item.orderStatus=='SHIPPED'? "bg-[blue]": item.orderStatus=='PENDING'? "bg-[red]": "bg-[gray]"} p-2 rounded`}>{item.orderStatus}</span></TableCell>



                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            </Card>
        </div>
    )
}

export default OrdersTableView