
import {FIND_PRODUCT_BY_ID_REQUEST, FIND_PRODUCT_BY_ID_SUCCESS, FIND_PRODUCT_BY_ID_FAILURE, FIND_PRODUCTS_REQUEST,  FIND_PRODUCTS_SUCCESS, FIND_PRODUCTS_FAILURE, CREATE_PRODUCT_REQUEST, CREATE_PRODUCT_SUCCESS, CREATE_PRODUCT_FAILURE, DELETE_PRODUCT_REQUEST, DELETE_PRODUCT_SUCCESS, DELETE_PRODUCT_FAILURE } from './ActionType'
import api from '../../config/apiConfig'
import API_BASE_URL from '../../config/apiConfig'


export const findProducts = (reqData) => async(dispatch) => {

    dispatch({type: FIND_PRODUCTS_REQUEST})
    //console.log("req data for color", reqData)
    const {color, sizes, minPrice, maxPrice, minDiscount, category, stock, sort, pageNumber, pageSize} = reqData
    try{
        //const test  = await api.get('/api/products?category=mens_kurta')
        //console.log("test: ",test)
        //console.log("product number actjion: " , reqData)
        const queryt = `/api/products?&category=${category}&color=${color}&size=${sizes}&minPrice=${minPrice}&maxPrice=${maxPrice}&minDiscount=${minDiscount}&stock=${stock}&sort=${sort}&pageNumber=${pageNumber}&pageSize=${pageSize}`


        const { data}= await api.get(`/api/products?&category=${category}&color=${color}&size=${sizes}&minPrice=${minPrice}&maxPrice=${maxPrice}&minDiscount=${minDiscount}&stock=${stock}&sort=${sort}&pageNumber=${pageNumber}&pageSize=${pageSize}`)
        //console.log("product data:" , data)
        dispatch({type: FIND_PRODUCTS_SUCCESS, payload: data})
        
    }catch(error){
        dispatch({type: FIND_PRODUCTS_FAILURE, payload: error.message})


    }
}
export const findProductsById = (reqData) => async (dispatch) => {

    dispatch({type: FIND_PRODUCT_BY_ID_REQUEST})
    const {productId} = reqData
    try{
        const { data}= await api.get(`/api/products/id/${productId}`)
        //console.log("Product Data in action: " , data)
        dispatch({type: FIND_PRODUCT_BY_ID_SUCCESS, payload: data})

    }catch(error){
        dispatch({type: FIND_PRODUCT_BY_ID_FAILURE, payload: error.message})


    }
}

export const createProduct = (product) => async (dispatch) =>{
    try{
        dispatch({type: CREATE_PRODUCT_REQUEST})
        console.log("to be created product" , product)
        const {data} = await api.post(`/api/admin/products`, product)
        console.log("createdProduct::::", data)
        dispatch({type: CREATE_PRODUCT_SUCCESS, payload: data})
    }catch(error){

        dispatch({type: CREATE_PRODUCT_FAILURE, payload : error.message})
    }
}

export const deleteProduct = (productId) => async (dispatch) =>{
    try{
        //console.log("deleete Product", productId)
        dispatch({type: DELETE_PRODUCT_REQUEST})
        const {data} = await api.delete(`/api/admin/products/${productId}`)
        dispatch({type: DELETE_PRODUCT_SUCCESS, payload: productId})
    }catch(error){

        dispatch({type: DELETE_PRODUCT_FAILURE, payload : error.message})
    }
}
