import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import SettingsCellIcon from '@mui/icons-material/SettingsCell';
import AttachMoneyIcon from '@mui/icons-material/AttachMoney';
import TimelineIcon from '@mui/icons-material/Timeline';
import MoreVertIcon from '@mui/icons-material/MoreVert';

import {Box, Avatar, Card, CardHeader, Grid, IconButton, Typography, CardContent} from '@mui/material';


const salesData = [
    {
        stats: '245k',
        title: 'Sales',
        color: '#e5d68a',
        icon: <TimelineIcon sx = {{fontSize: "1.75rem"}} />
    },
    {
        stats: '12.5k',
        title: 'Sales',
        color: '#22cb5c',
        icon: <AccountCircleIcon sx = {{fontSize: "1.75rem"}} />
    },
    {
        stats: '1.5k',
        title: 'Products',
        color: '#de4839',
        icon: <SettingsCellIcon sx = {{fontSize: "1.75rem"}} />
    },
    {
        stats: '88k',
        title: 'Revenue',
        color: '#12b0e8',
        icon: <AttachMoneyIcon sx = {{fontSize: "1.75rem"}} />
    },
]

const renderStats = () => {
    return salesData.map((item,index) => (
        <Grid item xs={12} sm={3} key={index}>
            <Box sx={{display: 'flex', alignItems: 'center'}}>
            <Avatar variant='rounded' sx={{mr: 3, width: 44, height: 44, boxShadow: 3, color: 'white', background:`${item.color}`}}>{item.icon}</Avatar>
            <Box sx={{display: "flex", flexDirection: 'column'}}>
                <Typography variant = 'caption'>{item.title}</Typography>
                <Typography variant = 'h6'>{item.stats}</Typography>
            </Box>
            </Box>
        </Grid>
    ))

}

const MonthlyOverview = () =>{
    return (
        <Card sx={{bgcolor: "#242b2e", color: 'white'}}>
            <CardHeader title="Monthly Overview" action={
                <IconButton size='small'>
                    <MoreVertIcon />
                </IconButton>
            }
            subheader={
                <Typography variant='body2'>

                    <Box component="span" sx={{fontWeight: 600, color: 'white'}}>
                        Total 48.5% growth&nbsp;
                    </Box>
                    this month
                </Typography>
            }

                        titleTypographyProps={{
                            sx:{
                                mb:2.5,
                                lineHeght:'2rem',
                                letterSpacing: '.15px'


                            }
                        }}
            />


            <CardContent sx={{pt:theme=>`${theme.spacing(3)}`}}>

                <Grid container spacing={[5,0]}>
                    {renderStats()}

                </Grid>
            </CardContent>
        </Card>
    )

}

export default MonthlyOverview
